.container {
    display: flex;
    flex-wrap: wrap;
    max-width: 1260px;
    padding: 20px 0px;
    margin: 0 auto;
    justify-content: space-between;
    margin: 40px 80px;
    gap: 40px;
    border-radius: 20px;
  }

.box {
    max-width: 300px;
    padding: 20px;
    background-color: #ccc;
    border-radius: 20px;

    .logo {
        width: 100%;
        height: 200px;
    }

    .title {
        padding: 20px;
    }
}